import classes from "./PetProfileForm.module.css";
import { ReactComponent as ImgNoFound } from "../../assets/imgNoFound.svg";
import { useNavigate } from "react-router-dom";

const PetProfileForm = () => {
  const navigate = useNavigate();
  const PreviousPage = () => {
    navigate("/pet");
  };

  const onSubmitHandler = (event) => {
    event.preventDefault();
  };

  return (
    <form onSubmit={onSubmitHandler} className={classes.petProfileForm}>
      <h1></h1>
      <div className={classes.actions}>
        <a href={"/pet"}>
          <ImgNoFound />
          <svg
            className="group-hover:text-blue-500 mb-1 text-gray-400"
            width="20"
            height="20"
            fill="currentColor"
            aria-hidden="true"
          >
            <path d="M10 5a1 1 0 0 1 1 1v3h3a1 1 0 1 1 0 2h-3v3a1 1 0 1 1-2 0v-3H6a1 1 0 1 1 0-2h3V6a1 1 0 0 1 1-1Z" />
          </svg>
          
        </a>
      </div>
      <div className={classes.control}>
        <div className={classes.selectBlock}>
          <div className={"m-2.5"}>
            <label></label>
            <select name="type">
              <option value="cat"></option>
              <option value="dog"></option>
            </select>
          </div>
          <div className={"m-2.5"}>
            <label></label>
            <select name="sex">
              <option value="male"></option>
              <option value="female"></option>
              <option value="unknown"></option>
            </select>
          </div>
          <div className={"m-2.5"}>
            <label></label>
            <select name="neutered">
              <option value="true"></option>
              <option value="false"></option>
            </select>
          </div>
        </div>
      </div>
      <div className={classes.control}>
        <label></label>
        <input placeholder={""} />
      </div>
      <div className={classes.control}>
        <label></label>
        <input placeholder={""} />
      </div>
      <div className={classes.control}>
        <label></label>
        <input placeholder={""} />
      </div>
      <div className={classes.control}>
        <label></label>
        <input type={"date"} />
      </div>
      <div className={classes.actions}>
        <button type={"submit"}></button>
      </div>
      <div className={classes.toggle}>
        <button type={"button"} onClick={PreviousPage}>
          
        </button>
      </div>
    </form>
  );
};

export default PetProfileForm;
